<template>
  <div>
    <div class="show-icon ">
      <i class="fa  myicon"  @click="tableShow">+</i>
    </div>


    <div v-if="tableShowFlag" class="panel panel-default templatemo-content-widget white-bg no-padding templatemo-overflow-hidden">
      <i class="fa fa-times"  @click="tableHidden"></i>
      <div class="panel-heading templatemo-position-relative"><h2 class="text-uppercase"><slot></slot></h2></div>
      <div class="table-responsive">
        <table class="table table-striped table-bordered">
          <thead>
          <tr>
            <td>No.</td>
            <td>测点</td>
            <td>数值</td>
            <td>单位</td>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>1.</td>
            <td>温度</td>
            <td>285</td>
            <td>C</td>
          </tr>
          <tr>
            <td>2.</td>
            <td>压力</td>
            <td>1.01325</td>
            <td>bar</td>
          </tr>
          <tr>
            <td>3.</td>
            <td>流量</td>
            <td>123</td>
            <td>kg/s</td>
          </tr>
          <tr>
            <td>4.</td>
            <td>焓值</td>
            <td>321.123</td>
            <td>kj/kg</td>
          </tr>

          </tbody>
        </table>
      </div>
    </div>
  </div>

</template>

<script>
    export default {
        name: "showTable",
      data(){
        return{
          tableShowFlag:true
        }
      },
      methods:{
        tableHidden(){
          this.tableShowFlag=false;
        },
        tableShow(){
          this.tableShowFlag=true;
        },
      }
    }
</script>

<style scoped>
  .panel{
    max-width: 300px ;
  }
  .show-icon{
    width: 20px;
    height: 20px;
    border-radius: 50%;
  cursor: pointer;
    color: #ffffff;
    background-color: rgba(58, 174, 180, 0.55);
  }
 .myicon {
    z-index: 1;
   font-weight: bolder;

 }
</style>



