<template>
  <div class="site-header">
    <div class="container">
      <div class="site-header-inner">
        <div class="brand header-brand">
          <h1 class="m-0">

            <router-link to="/" href="#">
              <svg width="32" height="32" viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg">
                <title>主页</title>

                <defs>
                  <radialGradient cy="0%" fx="50%" fy="0%" r="100%" id="logo-gradient">
                    <stop stop-color="#FFF" offset="0%"/>
                    <stop stop-color="#FFF" stop-opacity=".24" offset="100%"/>
                  </radialGradient>
                </defs>
                <path d="M16 32C7.163 32 0 24.837 0 16S7.163 0 16 0s16 7.163 16 16-7.163 16-16 16zm0-10a6 6 0 1 0 0-12 6 6 0 0 0 0 12z" fill="url(#logo-gradient)" fill-rule="evenodd"/>
              </svg>
            </router-link>
          </h1>
        </div>
        <ul class="header-links list-reset m-0">
          <li>
            <a href="#" @click="loginShow">登陆</a>
          </li>
          <li>
            <a class="button button-sm button-shadow" href="#">注册</a>
          </li>
        </ul>
      </div>
    </div>
    <!--遮罩层-->
    <div class="pet" v-if="petShowFlag" @click="loginHidden">
      <div class="login-in-up" @click.stop="voidEvent">
        <login-in></login-in>
      </div>
    </div>

  </div>
</template>

<script>
  import LoginIn from './loginIn.vue'

  export default {
    data(){
      return{
        loginShowFlag:false,
        petShowFlag:false
      }
    },
    methods:{
      loginShow(){
        this.loginShowFlag=true;
        this.petShowFlag=true;
      },
      loginHidden(){
        this.loginShowFlag=false;
        this.petShowFlag=false;
        console.log("2")

      },
      voidEvent(){
        console.log("1")
      }
    },
    components:{
      LoginIn
    }
  }
</script>

<style scoped>

  .site-header {
    position: relative;
    display: flex;
    justify-content: center;
    padding-top: 24px;
  }

  .site-header::before {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 500px;

    content: '';
    -webkit-transform: skewY(-12deg);
    transform: skewY(-12deg);
    -webkit-transform-origin: 0;
    transform-origin: 0;

    background: #0F8A9D;
    background: linear-gradient(57deg, #00C6A7 0%, #1E4D92 100%);

  }

  .container{
    width: 100%;
    max-width: 1128px;
    margin: 0 auto;
    padding-right: 16px;
    padding-left: 16px;
    margin: 0;
    box-sizing: border-box;
  }



  .site-header-inner {

    position: relative;

    display: flex;
    padding: 0;
    justify-content: space-between;
    align-items: center;

    font-size: 1rem;
  }

  .m-0 {
    margin: 0;
  }


  .header-links {
    display: inline-flex;
    margin: 0;
    padding: 0;
  }

  .header-links li {
    display: inline-flex;
  }

  .header-links li{
    margin: 0;
    padding: 0;
    cursor: pointer;
  }


  .header-links li a:not(.button) {

    font-size: 14px;
    line-height: 14px;

    padding: 8px 24px;

    text-decoration: none;
    letter-spacing: 0;
    text-transform: uppercase;

    color: rgba(255, 255, 255, 0.7);
  ;
  }

  .header-links a:not(.button):hover ,
  .header-links a:not(.button):active {
    color: #FFFFFF;
  }
  .button {

    font-size: 14px;
    line-height: 14px;

    padding: 8px 24px;

    text-decoration: none;
    letter-spacing: 0;
    text-transform: uppercase;

    color: rgba(255, 255, 255, 0.7);
    border-radius: 2px;
    background-color: #FFFFFF;
    justify-content: center;

    color: #00A2B8 !important;
  }


  .button-sm.button-shadow::before {
    box-shadow: 0 4px 16px rgba(32, 43, 54, 0.12);
  }

  .button-sm.button-shadow:hover::before {
    box-shadow: 0 4px 16px rgba(32, 43, 54, 0.25);
  }

  .pet{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .pet .login-in-up{
    width: 600px;
  }

</style>




