import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import index from '@/pages/index'
import showPage from '@/pages/showPage'

import test_swiper from '@/test/test_swiper'
import navCommon from '@/components/navCommon'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/showPage',
      name: 'showPage',
      component: showPage
    },
    {
      path: '/test_swiper',
      name: 'test_swiper',
      component: test_swiper
    },
    {
      path: '/navCommon',
      name: 'navCommon',
      component: navCommon
    }
  ]
})



