<template>
  <div class="m-50">
  <div class="templatemo-content-widget templatemo-login-widget white-bg">
    <header class="text-center">
      <!--<div class="square"></div>-->
      <h1>请登录您的账户</h1>
    </header>
    <form action="index.html" class="templatemo-login-form">
      <div class="form-group">
        <div class="input-group">
          <div class="input-group-addon"><i class="fa fa-user fa-fw"></i></div>
          <input type="text" class="form-control" placeholder="用户名">
        </div>
      </div>
      <div class="form-group">
        <div class="input-group">
          <div class="input-group-addon"><i class="fa fa-key fa-fw"></i></div>
          <input type="password" class="form-control" placeholder="密码">
        </div>
      </div>
      <div class="form-group">
        <div class="checkbox squaredTwo">
          <input type="checkbox" id="c1" name="cc" />
          <label for="c1"><span></span>记住账户</label>
        </div>
      </div>
      <div class="form-group">
        <!--<router-link class="button button-secondary button-shadow"  href="#" to="/">-->
        <router-link href="javascript:void(0);" :to="{path:'/showpage'}">
        <button type="submit" class="templatemo-blue-button width-100">登陆</button>
        </router-link>
      </div>
    </form>
  </div>
  <div class="templatemo-content-widget templatemo-login-widget templatemo-register-widget white-bg">
    <p>想要使用我们的产品吗？ <strong><a href="#" class="blue-text">联系我们</a></strong></p>
  </div>
  </div>
</template>

<script>

  import './../assets/css/font-awesome.min.css'
  import './../assets/css/bootstrap.min.css'
  import './../assets/css/templatemo-style.css'


  export default {
        name: "loginIn"
    }
</script>

<style scoped>
.m-50{
  margin-top: 0px;

}
</style>



